body {
    background: rgb(255,133,89);
    background: linear-gradient(0deg, rgb(255, 174, 81) 0%, rgb(255, 165, 133) 100%);
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    color: #333;
    min-height: 100vh;
  }
  
  h1 {
      font-size: 4.3em;
      font-weight: 300;
  }
  
  h2 {
      color: #333;
      font-size: 1.8em;
      font-weight: 200;
      padding: 5px;
  }


  hr {
      border: none;
      border-top: 3px double #333;
      color: #333;
  }
  
  .taskText {
      font-weight: 700;
  }
  
  .taskDivider {
      border: none;
      width: 93%;
      border-top: 1px dotted rgb(223, 37, 4);;
      color: #fff;
  }
  
  ul {
      list-style-type: none;
      margin: 0;
      padding: 0;
  }
  
.small-text {
    font-size: 0.8em;
    font-weight: 500;
    color:  rgb(175, 0, 0);
}

  .btn {
      background-color: rgb(175, 0, 0); 
      border: none;
      color: white;
      margin: 4px;
      font-size: 0.9em;
      cursor: pointer;
      float: right;
  }
    
  .btn:hover, .btn:focus {
      background-color: rgb(253, 14, 14); 
      color: white;
  }
  
  .icon-todo { 
      font-size: 1.8em;
      color: rgb(175, 0, 0);
      padding-right: 5px;
      padding-left: 5px;
      float: right;
  }
    
  .icon-todo:hover { 
      color: rgb(253, 14, 14); 
      cursor: pointer;
  }
    
  .icon-bin { 
      font-size: 1.8em;
      color: rgb(175, 0, 0);
      padding-right: 5px;
      padding-left: 5px;
      float: right;
      cursor: pointer;
  }
    
  .icon-bin:hover { 
      color: rgb(253, 14, 14); 
  }